<template>
   <group class="jq-mt">
   <popup-picker :title="dateTile" :data="timeList" :value.sync="time" ></popup-picker>
   </group>
 <group class="jq-mt">
   <div class="chart" v-ebarline="barLineData"></div>
    </group>
</template>
  <script>
    import {Group,Selector,PopupPicker} from 'vux/src/components'
    import { getList } from '../../../untils/api'
    export default {
    components: {
    Group,
    PopupPicker,
    Selector
    },
    data () {
    return {
    dateTile:'日期',
    time: formatData.defaultTime,
    timeList: formatData.dateList,
    permissions:localStorage.getItem("permissions"),
    barLineData:{}
    }
    },
    ready(){
     document.title='SMD各部一次良率';
    },
    methods: {
    async getData() {
        commit('UPDATE_LOADING', true)
    let id ='';
     if(this.permissions=='201'){//fpi
       id ='APP10305a';
      }else if(this.permissions=='203'){
          id ='APP10305b';
      }
    let date=formatData.dateConversion(this.time);
    let dataTit = await getList(id,{date_:date});
    if(dataTit.success){
   
    //表头：
    //表格:
    const TableList = dataTit.data;
    this.barLineData = {};
    this.barLineData.dataset = formatData.formatLessThanTwoLatitudeData(TableList);
      commit('UPDATE_LOADING', false)
    }else{
        commit('UPDATE_LOADING', false)
    console.log(dataTit.message);
    }

    }
    },
    watch: {
     time (newtime, oldtime) {
      this.getData();
       formatData.setDateTime(newtime);
     }
    },
    created: function () {
    this.barLineData = {};
    this.barLineData.dataset = [];
    this.getData();
    }
    }
  </script>






